.fixed-iisus{
  position: fixed !important;
}

.header {
  margin-bottom: 30px !important;
  background-color: #fff !important;
  padding: 0px 22px 0px 0px !important;
  border-radius: 6px !important;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07) !important;
}

.header .header_navlink {
  padding: 0;
}

.header .header_link {
  font-weight: 500;
  color: darkslategray !important;
}

.header .header_link:hover {
  font-weight: 500;
  color: var(--main-color) !important;
}

.header .header_link_active {
  font-weight: 500;
  color: var(--main-color) !important;
}

.header .header_home {
  background-color: var(--main-color);
  padding: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.navbar_collapse{
  justify-content: space-between;
}

.header .header_left{
  margin: auto;
  margin-left: 10px;
}

.header .header_right {
  display: flex !important;
  align-items: center !important;
}
.header .header_right > a > .MuiSvgIcon-root {
  font-size: 16px !important;
  margin-right: 12px;
  color: #000 !important;
}

.mobile_social_icons{
  display:none;
}

@media (max-width: 991px) {

  .navbar_collapse{
    display: flex;
    flex-direction: column;
  }

  .header .header_left{
    margin: auto;
    text-align: center !important;
  }

  .header .header_right {
    margin-bottom: 15px;
  }
  .mobile_social_icons{
    display: inline-block !important;
  }

  .mobile_social_icons > a > .MuiSvgIcon-root {
    font-size: 16px !important;
    margin-right: 12px;
    color: #000 !important;
  }
  .not-show-on-smaller-screen{
    display: none;
  }
}
